<template>
  <div class="game">
    <div class="container">
      <nav class="nav">
        <h1>Speed Typing Game</h1>
      </nav>

      <section class="level">
        <h2>Select Level</h2>
        <button @click="easyLevel">Easy</button>
        <button @click="mediumLevel">Medium</button>
        <button @click="hardLevel">Hard</button>
      </section>
      <p class="levelText">
        {{ level }}
      </p>
      <section class="content">
        <h3>
          Type the given word within
          <span class="seconds">{{ seconds }}</span> seconds
        </h3>
        <h1>{{ currentWord }}</h1>
        <form ref="form" @submit.prevent="getValue(), addScore()">
          <input
            type="text"
            placeholder="Start typing"
            v-model="input"
            name="inputValue"
          />
        </form>

        <h4 class="message">{{ message }}</h4>
        <div class="score">
          <h4>Time Left: {{ time }}</h4>
          <h4>Score: {{ score }}</h4>
        </div>

        <button class="newGame" @click="refresh">New Game</button>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "main",
  data() {
    return {
      seconds: 0,
      easy: 7,
      medium: 5,
      hard: 3,
      level: "",
      input: "",
      message: "",
      isPlaying: true,
      currentWord: "",
      time: 0,
      score: 0,
      words: [
        "hello",
        "mirafzal",
        "hat",
        "river",
        "statue",
        "generate",
        "stubborn",
        "cocktail",
        "sugar",
        "nonsense",
        "jealous",
        "impatient",
        "joke",
        "css",
        "vue",
        "javascript",
        "algorithm",
        "own",
        "other",
        "old",
        "right",
        "big",
        "high",
        "different",
        "small",
        "large",
        "next",
        "early",
        "young",
        "important",
        "few",
        "public",
        "bad",
        "same",
        "able",
        "someone",
        "learn",
        "browser",
        "mother",
        "programmer",
        "driver",
        "accent",
        "accept",
        "phone",
        "wave",
        "brother",
        "lizard",
        "agains",
      ],
    };
  },

  methods: {
    easyLevel() {
      this.seconds = this.easy;
      this.displayTime();
      this.countDown();
      this.level = "easy";
    },
    mediumLevel() {
      this.seconds = this.medium;
      this.displayTime();
      this.countDown();
      this.level = "medium";
    },
    hardLevel() {
      this.seconds = this.hard;
      this.displayTime();
      this.countDown();
      this.level = "hard";
    },
    getValue() {
      const inputValues = this.$refs.form.inputValue.value;
      if (inputValues === this.currentWord) {
        this.message = "CORRECT";
        this.currentWord = this.getRandomWord();
        this.input = "";
        this.score++;
        this.resetTimer();
      } else {
        this.message = "Wrong Answer";
      }
    },
    displayTime() {
      this.time = this.seconds;
    },
    countDown() {
      setInterval(() => {
        let countDownTime = this.time;
        if (countDownTime > 0) {
          this.time--;
        } else {
          this.isPlaying = false;
          this.message = "Gameover";
          this.input = "";
        }
      }, 1300);
    },
    resetTimer() {
      if (this.level === "easy") {
        this.time = this.easy;
      } else if (this.level === "medium") {
        this.time = this.medium;
      } else if (this.level === "hard") {
        this.time = this.hard;
      }
    },
    addScore() {
      let inputValue = this.$refs.form.inputValue.value;
      if (inputValue === this.currentWord) {
        this.words;
      }
    },

    refresh() {
      window.location.reload();
    },
    getRandomWord() {
      let random = Math.floor(Math.random() * this.words.length);
      return this.words[random];
    },
  },
  mounted() {
    this.currentWord = this.getRandomWord();
  },
};
</script>


